.terms-of-service {
  min-height: 100vh;
  background: linear-gradient(135deg, #f8fafc 0%, #e2e8f0 100%);
  padding: 80px 20px 40px;
}

.terms-container {
  max-width: 800px;
  margin: 0 auto;
  background: white;
  border-radius: 12px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.1);
  overflow: hidden;
}

.terms-header {
  background: linear-gradient(135deg, #00254c 0%, #003d7a 100%);
  color: white;
  padding: 40px;
  text-align: center;
}

.terms-header h1 {
  font-size: 2.5rem;
  margin-bottom: 10px;
  font-weight: 700;
}

.last-updated {
  font-size: 1rem;
  opacity: 0.9;
  margin: 0;
}

.terms-content {
  padding: 40px;
  line-height: 1.7;
  color: #333;
}

.terms-content section {
  margin-bottom: 40px;
}

.terms-content h2 {
  color: #00254c;
  font-size: 1.5rem;
  margin-bottom: 15px;
  padding-bottom: 10px;
  border-bottom: 2px solid #e2e8f0;
}

.terms-content h3 {
  color: #003d7a;
  font-size: 1.2rem;
  margin: 20px 0 10px 0;
}

.terms-content p {
  margin-bottom: 15px;
  text-align: justify;
}

.terms-content ul {
  margin: 15px 0;
  padding-left: 20px;
}

.terms-content li {
  margin-bottom: 8px;
}

.contact-info {
  background: #f8fafc;
  padding: 20px;
  border-radius: 8px;
  border-left: 4px solid #00254c;
}

.contact-info p {
  margin-bottom: 8px;
}

.terms-footer {
  padding: 30px 40px;
  text-align: center;
  background: #f8fafc;
  border-top: 1px solid #e2e8f0;
}

.back-button {
  background: linear-gradient(135deg, #00254c 0%, #003d7a 100%);
  color: white;
  border: none;
  padding: 12px 30px;
  border-radius: 8px;
  font-size: 1rem;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
}

.back-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 25px rgba(0, 37, 76, 0.3);
}

/* Responsividade */
@media (max-width: 768px) {
  .terms-of-service {
    padding: 40px 15px 20px;
  }
  
  .terms-header {
    padding: 30px 20px;
  }
  
  .terms-header h1 {
    font-size: 2rem;
  }
  
  .terms-content {
    padding: 30px 20px;
  }
  
  .terms-content h2 {
    font-size: 1.3rem;
  }
  
  .terms-footer {
    padding: 20px;
  }
}
